.form {
  width: 100%;
  max-width: 652px;
  display: flex;
  flex-direction: column;
}

.form__title {
  display: block;
  font-family: @jost;
  font-size: 28px;
  font-weight: 500;
  line-height: 40px;
  margin-bottom: 44px;
  margin-top: 0;

  @media screen and (max-width: @1024) {
    margin-bottom: 24px;
  }
}

.form__input {
  font-size: 16px;
  line-height: 19px;
  color: @black;
  padding-top: 16px;
  padding-bottom: 16px;
  border: none;
  border-bottom: 1px solid @gray;
  margin-bottom: 36px;
  outline: none;
  opacity: 0.5;

  @media screen and (max-width: @1024) {
    margin-bottom: 14px;
  }

  &:hover {
    opacity: 0.8;
  }

  &:active,
  &:focus {
    opacity: 0.6;
  }

  &::placeholder {
    font-size: 16px;
    line-height: 19px;
    color: @black;
    opacity: 0.5;
  }
}

textarea {
  resize: none;
  font-size: 16px;
  line-height: 19px;
  color: @black;
  padding-top: 16px;
  padding-bottom: 16px;
  border: none;
  border-bottom: 1px solid @gray;
  margin-bottom: 36px;
  outline: none;
  opacity: 0.3!important;

  :hover {
    opacity: 0.8!important;
  }

  &:active,
  &:focus {
    opacity: 0.6!important;
  }
}

.form__submit {
  max-width: 265px;
  border: none;
  padding-left: 50px;
  padding-right: 50px;
  margin-top: 16px;

  @media screen and (max-width: @1024) {
    margin-top: 8px;
  }
}

.profile--form {
  max-width: 866px;
  width: 100%;

  .form__ckeck-lic {
    display: flex;
    margin-bottom: 46px;
  }

  .form__checkbox-wrapper {
    margin-right: 20px;

    .catalog__params-label {
      font-family: @roboto;
      font-size: 16px;
      font-weight: 400;
      line-height: 19px;
      letter-spacing: 0.08em;
      border-radius: 44px;
      border: 1px solid @black;
      text-transform: uppercase;

      @media screen and (max-width: @576) {
        text-align: center;
      }
    }
  }

  .form__text-field {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    .form__input {
      max-width: 403px;
      width: 100%;
    }
  }

  .form__submit {
    max-width: 300px;
    width: 100%;
    padding-left: 44px;
    padding-right: 44px;
    text-align: center;

    @media screen and (max-width: @576) {
      display: block;
      margin-left: auto;
      margin-right: auto;
    }
  }

  .form__delete-profile {
    font-family: @roboto;
    font-size: 16px;
    font-weight: 400;
    line-height: 19px;
    opacity: 0.5;
    background-color: transparent;
    border: none;
    margin-left: 40px;

    @media screen and (max-width: @576) {
      display: block;
      margin-left: auto;
      margin-right: auto;
      margin-top: 40px;
    }
  }
}
