.personal-area__main-wrapper {
  display: flex;
  justify-content: space-between;
  padding-top: 80px;
  padding-bottom: 150px;

  @media screen and (max-width: @1024) {
    padding-top: 20px;
    flex-direction: column;
  }
}

.personal-area__content {
  width: 100%;
  max-width: 866px;

  @media screen and (max-width: @768) {
    overflow-x: scroll;
    width: calc(100% + 20px);
  }
}

.personal-area__filters-btn {
  display: none;
  font-size: 16px;
  font-weight: 700;
  margin-bottom: 30px;
  padding: 15px 21px;
  letter-spacing: .08em;
  text-transform: uppercase;
  color: #34393b;
  border: 1px solid #eaeae8;
  background-color: transparent;
  margin-top: 30px;
  width: 160px;

  svg {
    width: 32px;
    height: 32px;
  }

  @media screen and (max-width: @768) {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
  }
}
