.compilation {
  padding-top: 30px;
  padding-bottom: 80px;
}

.compilation__breadcrumbs {
  margin-bottom: 48px;
}

.compilation__info {
  display: flex;
  justify-content: space-between;
  margin-bottom: 72px;

  img {
    @media screen and (max-width: @1024) {
      width: 48%;
      height: auto;
    }

    @media screen and (max-width: @768) {
      width: 100%;
    }
  }

  @media screen and (max-width: @768) {
    flex-direction: column;
  }
}

.compilation__txt {
  max-width: 592px;
  width: 100%;

  @media screen and (max-width: @1024) {
    width: 48%;
  }

  @media screen and (max-width: @768) {
    width: 100%;
    margin-top: 40px;
  }
}

.compilation__title {
  .small-title();
  margin-bottom: 24px;
  text-align: left;
}

.compilation__dscr {
  font-family: @roboto;
  font-size: 16px;
  font-weight: 400;
  line-height: 26px;
}

.catalog__list--compilation {
  .catalog__item {
    width: 25%;
    max-width: 306px;
    margin-right: auto!important;

    @media screen and (max-width: @1024) {
      max-width: 240px;
    }

    &:nth-child(4n) {
      margin-right: 0;
    }

    &:nth-child(2n -1) {
      margin-left: 0;
    }

    @media screen and (max-width: @768) {
      margin-left: auto!important;
      margin-right: auto!important;
      max-width: 48%;
      width: 48%;
    }

    @media screen and (max-width: @576) {
      margin-left: auto!important;
      margin-right: auto!important;
      max-width: 100%;
      width: 100%;
    }
  }

  .catalog__img {
    width: 100%;
    height: auto;
  }
}
