.side-bar {
  max-width: 306px;
  width: 100%;

  @media screen and (max-width: @1024) {
    max-width: 100%;
    margin-top: 30px;
    margin-bottom: 30px;
  }

  @media screen and (max-width: @768) {
    margin-top: 0;
    display: none;
  }
}

.side-bar__list {
  @media screen and (max-width: @1024) {
    display: flex;
    justify-content: space-between;
  }

  @media screen and (max-width: @768) {
    flex-direction: column;
  }
}

.side-bar__item {
  margin-bottom: 17px;
}

.side-bar__link {
  font-family: @jost;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 29px;
  color: @black;
  padding-left: 34px;
  position: relative;
}

.side-bar__link--active {
  &::after {
    content: "";
    display: block;
    width: calc(100% - 34px);
    height: 2px;
    background-image: @gradient;
    position: absolute;
    bottom: -2px;
    left: 34px;
  }
}

.side-bar__link--profile {
  &::before {
    content: "";
    width: 15px;
    height: 20px;
    display: block;
    background-image: url("../img/profile-icon.svg");
    background-size: 15px 19px;
    background-position: 0 0;
    background-repeat: no-repeat;
    position: absolute;
    top: 4px;
    left: 0;
  }
}

